let _debounceTimeout = null,
	_throttleRunning = false
	
/**
 * 
 *  https://juejin.cn/post/6844903669389885453
 * 
 * @param {Function} 
 * @param {Number} delay ms   
 */
export const debounce = (fn, delay=500) => {
	clearTimeout(_debounceTimeout);
	_debounceTimeout = setTimeout(() => {
		fn();
	}, delay);
}

/**
 * 
 *  https://juejin.cn/post/6844903669389885453
 * 
 * @param {Function} 
 * @param {Number} delay ms  
 */
export const throttle = (fn, delay=500) => {
	if(_throttleRunning){
		return;
	}
	_throttleRunning = true;
	fn();
	setTimeout(() => {
	    _throttleRunning = false;
	}, delay);
}

/**
 * toast 
 * 
 * @param {String} title 
 * @param {Object} param 
 * @param {Integer} param.duration 
 * @param {Boolean} param.mask 
 * @param {Boolean} param.icon          
 */
export const msg = (title = '', param={}) => {
	if (!title) {
		return;
	}
	uni.showToast({
		title,
		duration: param.duration || 1500,
		mask: param.mask || false,
		icon: param.icon || 'none' // TODO  error 
	});
}

/**
 * 
 * 
 * @param {Boolean} options.nav  true
 * @return {Boolean} 
 */
export const isLogin = (options = {}) => {
	const token = getAuthToken();
	if (token) {
		return true;
	}
	//  nav  false
	if (options.nav !== false) {
		uni.navigateTo({
			url: '/pages/auth/login'
		})
	}
	return false;
}

/**
 *  Token
 * 
 * @return  Token
 */
export const getAuthToken = () => {
	return uni.getStorageSync('token');
}

/**
 * 
 *  
 * @param {String} 
 * @param {String}   mobile tel  TODO 
 */
export const checkStr = (str, type) => {
	switch (type) {
		case 'mobile': //
			return /^1[3|4|5|6|7|8|9][0-9]{9}$/.test(str);
		case 'tel': //
			return /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/.test(str);
		case 'card': //
			return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(str);
		case 'mobileCode': //6
			return /^[0-9]{6}$/.test(str)
		case 'pwd': //6~18
			return /^([a-zA-Z0-9_]){6,18}$/.test(str)
		case 'payPwd': // 6
			return /^[0-9]{6}$/.test(str)
		case 'postal': //
			return /[1-9]\d{5}(?!\d)/.test(str);
		case 'QQ': //QQ
			return /^[1-9][0-9]{4,9}$/.test(str);
		case 'email': //
			return /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/.test(str);
		case 'money': //(2)
			return /^\d*(?:\.\d{0,2})?$/.test(str);
		case 'URL': //
			return /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(str)
		case 'IP': //IP
			return /((?:(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d)\\.){3}(?:25[0-5]|2[0-4]\\d|[01]?\\d?\\d))/.test(str);
		case 'date': //
			return /^(\d{4})\-(\d{2})\-(\d{2}) (\d{2})(?:\:\d{2}|:(\d{2}):(\d{2}))$/.test(str) || /^(\d{4})\-(\d{2})\-(\d{2})$/
				.test(str)
		case 'number': //
			return /^[0-9]$/.test(str);
		case 'english': //
			return /^[a-zA-Z]+$/.test(str);
		case 'chinese': //
			return /^[\\u4E00-\\u9FA5]+$/.test(str);
		case 'lower': //
			return /^[a-z]+$/.test(str);
		case 'upper': //
			return /^[A-Z]+$/.test(str);
		case 'HTML': //HTML
			return /<("[^"]*"|'[^']*'|[^'">])*>/.test(str);
		default:
			return true;
	}
}