/* eslint-disable */
import Request from './request';
import { refreshToken } from '@/utils/request';
import indexConfig from '@/config/index.config';
import mHelper from '@/utils/helper';
import store from '@/store';

const http = new Request();
// request
http.setConfig(config => {
	/*  */
	config.baseUrl = indexConfig.baseUrl; /*  */
	const systemInfo = uni.getSystemInfoSync();
	const systemInfoHeaders = {
		'device-name': systemInfo.brand, // 
		width: systemInfo.screenWidth, // 
		height: systemInfo.screenHeight, // 
		os: systemInfo.platform, // 
		'os-version': systemInfo.system ,// 
	};
	config.header = {
		...config.header,
		...systemInfoHeaders
	};
	return config;
});

let isRefreshing = false;
let requests = [];
http.interceptor.request(
	config => {
		/*  */
		config.header['Authorization'] = 'Bearer ' + store.state.token;
		// 
		config.header['tenant-id'] = uni.getStorageSync('tenantId') || 1;
		return config;
	},
	error => {
		return Promise.reject(error);
	}
);

// refreshToken
async function handleRefreshToken(refresh_token) {
	const params = {};
	params.group = mHelper.platformGroupFilter();
	params.refresh_token = refresh_token;
	await http.post(refreshToken, params).then(async r => {
		store.commit('login', r.data);
		isRefreshing = false;
	});
}
http.interceptor.response(
	async response => {
		const code = response.data.code || 200;
		// 
		// var status=response.data.status=='Y'?"200":"400";
		console.log("zhelu :"+code)
		
		/*  */
		switch (parseInt(code)) {
			case 200:
				return response.data;
			case 400:
				mHelper.toast('');
				return Promise.reject(response.data.msg);
				break;
			case 401:
				isRefreshing = false;
				// refreshToken 401
				if (response.config.url === refreshToken) {
					uni.removeStorageSync('accessToken');
					await store.commit('logout');
					uni.showModal({
						content: '',
						success: confirmRes => {
							if (confirmRes.confirm) {
								mHelper.backToLogin();
								throw response.data;
							}
							if (confirmRes.cancel) {
								mHelper.prePage();
								throw response.data;
							}
						}
					});
					break;
				} else {
					// refreshToken 
					if (!store.state.refreshToken) {
						uni.removeStorageSync('accessToken');
						await store.commit('logout');
						mHelper.backToLogin();
						// uni.showModal({
						// 	content: '',
						// 	success: confirmRes => {
						// 		if (confirmRes.confirm) {
						// 			mHelper.backToLogin();
						// 			throw response.data.message;
						// 		}
						// 	}
						// });
						throw response.data.msg;
					} else {
						// isRefreshing
						if (!isRefreshing) {
							isRefreshing = true;
							// token
							await handleRefreshToken(store.state.refreshToken, response);
							requests.forEach(cb => cb());
							requests = [];
							isRefreshing = false;
							return http.request(response.config);
						} else {
							return new Promise(resolve => {
								// resolvetoken
								requests.push(() => {
									resolve(http.request(response.config));
								});
							});
						}
					}
				}
				break;
			case 405:
				mHelper.toast('');
				return Promise.reject(response.data.msg);
			case 404:
				mHelper.toast(response.data.msg);
				return Promise.reject(response.data.msg);
			case 1000:
				mHelper.toast(response.data.msg);
				return Promise.reject(response.data.msg);
			case 429:
				mHelper.toast('');
				return Promise.reject(response.data.msg);
			case 500:
				// mHelper.toast('');
				return Promise.reject(response.data.msg);
			case 3003://
				return response.data;
			default:
				mHelper.toast(response.data.msg);
				return Promise.reject(response.data.msg);
		}
	},
	error => {
		return Promise.reject(error);
	}
);

export { http };
